
import dateFormat from 'vux/src/tools/date/format.js'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: '',
components: {
dateFormat,
LoadMore,
Toast
},
data () {
return {
tabIndex: 0,
tabs: ['积分明细', '积分收入', '积分支出'],
integral: '',
lists: [],
json: {
order: 'ASC',
pageNum: 1,
pageSize: 10,
typeid: 0
},
loading: false,
isLastPage: false
}
},
created () {
let integral = localStorage.getItem('integral') || ''
this.integral = integral
if (!integral) {
this.getDetail()
}
this.getLists()
},
methods: {
tab (index) {
console.log(index)
this.tabIndex = index
this.json.typeid = index
this.json.pageNum = 1
this.isLastPage = false
this.lists = []
this.getLists()
},
getDetail () {
this.axios.post('member/detail').then((response) => {
if (response.success) {
this.detail = response.data
this.integral = response.data.integral
localStorage.setItem('integral', response.data.integral)
}
})
},
getLists () { // typeid:1积分收入、2积分支出
this.axios.post('member/integrallist', this.json).then((response) => {
if (response.success) {
let data = response.data
console.log(data)
if (this.isLastPage) { return }
if (data.isLastPage) {
this.isLastPage = true
}
let lists = data.list
lists.map((item, index) => {
if (item.orderid) {
item.messageDetail = `
订单编号：${item.orderid}
`
} else {
item.messageDetail = item.context
}
item.createDate1 = dateFormat(item.createDate, 'YYYY-MM-DD HH:mm:ss')
})
if (this.json.pageNum / 1 === 1) {
this.lists = lists
} else {
this.lists = this.lists.concat(lists)
}
this.loading = false
this.json.pageNum++
}
})
},
loadMore () { // 上拉加载更多
if (this.isLastPage) {
return
}
this.loading = true

}
}
}



